body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #fc354c, #0abfbc);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ba {
  border-style: solid;
  border-width: 1px;
}

.b--black-10 {
  border-color: rgba(0, 0, 0, .1);
}

.b--black-05 {
  border-color: rgba(0, 0, 0, .05);
}

.br3 {
  border-radius: .5rem;
}

.br-100 {
  border-radius: 100%;
}

.dib {
  display: inline-block;
}

.fw4 {
  font-weight: 400;
}

.h4 {
  height: 8rem;
}

.mw5 {
  max-width: 16rem;
}

.w4 {
  width: 8rem;
}

.gray {
  color: #777;
}

.bg-white {
  background-color: #fff;
}

.pa2 {
  padding: .5rem;
}

.pa3 {
  padding: 1rem;
}

.mb2 {
  margin-bottom: .5rem;
}

.mt0 {
  margin-top: 0;
}

.mv3 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.tc {
  text-align: center;
}

.f3 {
  font-size: 1.5rem;
}

.f5 {
  font-size: 1rem;
}

.center {
  margin-right: auto;
  margin-left: auto;
}

@media screen and (min-width: 30em) {
  .pa4-ns {
      padding: 2rem;
  }
}